.app__menuItem {
    width: 100%;
    margin: 1rem 0;

    display: flex;
    flex-direction: column;
}

.app__menuItem-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__menuItem-sub {
    width: 100%;
    margin-top: 0.2rem;
}

.app__menuItem-name {
    flex: 1;

}
.app__menuItem-dash{
    position: static;
    width: 120px;
    height: 1px;

    background-color: white;
    margin: 0 1rem;
}

.app__meniItem-price {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: row;
}

